:root {
  --text--white: #fff;
  --hero-background: #f24c26;
}

header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 20px;
  text-align: center;
  z-index: 1000; /* Ensure it stays on top */
}

.nav-component ul {
  display: flex;
  justify-content: space-around;
  list-style-type: none;
}

.nav-component ul li {
  padding: 10px;
  font-size: 1.2rem;
}

.nav-component ul li a {
  text-decoration: none;
  color: var(--text--white);
}

.nav-end {
  display: flex;
}

.hero-component {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  color: var(--text--white);
  background-color: var(--hero-background);
}

.hero-component h1 {
  font-size: 3rem;
}

.quote-component,
.quote-component--purple {
  display: flex;
  justify-content: center;
  align-items: left;
  flex-direction: column;
  height: 100vh;
  color: var(--text--white);
  background-color: #1e01ad;
  padding: 0 0.5rem;
}

.quote-component span,
.quote-component--purple span {
  font-size: 2.7rem;
  font-weight: bold;
}

.quote-component span:nth-child(2) {
  padding-left: 4rem;
}

.quote-component span:nth-child(3) {
  padding-left: 8rem;
}

.about-component__info {
  position: relative;
  margin: 4rem 2rem;
}

.about-component__info h2 {
  text-align: center;
}

.underline--full {
  height: 3px;
  background-color: black;
  width: 30%;
  margin: auto;
}

.underline--half {
  height: 3px;
  background-color: var(--hero-background);
  width: 15%;
  margin: auto;
  position: absolute;
  left: 0;
  right: 0;
  top: 36;
  z-index: 1000;
}

.about-component__info p {
  margin: 2rem 0;
}

.quote-component--purple {
  background-color: #6301ad;
}

.quote-component--purple span {
  color: var(--text--white);
}

.quote--right{
  text-align: right;
}
